body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav,
.list-group-item,
a {
  color: #551b14;
}

h1 {
  color: #551b14;
  font-family: fantasy;
  background-color: #b59da4;
}

h3 {
  color: #6d3d14;
  font-family: fantasy;
  -webkit-text-decoration: underline double;
          text-decoration: underline double;
  -webkit-text-decoration-color: #551b14;
          text-decoration-color: #551b14;
  /* border-bottom: 0px solid #000; */
}

h2,
.aboutMe {
  color: #6d3d14;
  font-family: fantasy;
}
.App-footer {
  color: #551b14;
}

.body {
  background-color: #cdc5b4;
}

.contact {
  color: #551b14;
  font-size: large;
}
.App {
  text-align: center;
  font-family: fantasy;
  background-color: #cdc5b4;
}

.list-group .list-group-item {
  background-color: #cdc5b4;
  font-size: larger;
  border: none;
}

.navbackground ul,
.navbackground ul .list-group-item {
  background-color: #b59da4;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.aboutMe {
  font-size: large;
  margin-left: 10px;
  margin-right: 10px;
}

.mePic {
  height: 40vmin;
  border: #6d3d14 6px double;
}

.formbg {
  background-color: #cdc5b4;
}

.list {
  flex-shrink: 1;
  flex-direction: row;
  font-size: 25px;
  margin-right: 20px;
  color: #551b14;
  list-style-type: none;
  text-shadow: 5px 5px 5px grey;
}

.flexcont {
  display: flex;
  flex-direction: column;
  flex-basis: 50;
  background-color: #cdc5b4;
}
.projects {
  display: block;
  max-width: 50%;
  margin-left: 25%;
  justify-content: center;
  align-items: center;
  padding: 2%;
}

.form {
  margin: 20px;
}

.form input,
textarea {
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
}

.formfield {
  margin-left: 25%;
  margin-bottom: 2%;
}
/* 
.message {
  height: 100px;
} */

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  text-shadow: 10px 10px 5px grey;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#app {
  background-color: #cdc5b4;
}

#linkedin {
  height: 50px;
  padding: 10px;
}

#stack {
  height: 50px;
  padding: 10px;
}

#github {
  height: 50px;
  padding: 10px;
}

#nav {
  color: #551b14;
}

#projlinks {
  color: #6d3d14;
}

#resume {
  color: #6d3d14;
  text-decoration: overline;
}

#my-projects {
  padding: 3%;
}

.list-group .list-group-item {
  color: #6d3d14;
  font-family: fantasy;
}

